/*** Import Fonts ***/
@import url(https://fonts.googleapis.com/css?family=Lato:400,100,300,300italic,700);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,300,400italic,300italic,200,600);
@import url(https://fonts.googleapis.com/css?family=PT+Sans+Narrow:400,700);
@import url(https://fonts.googleapis.com/css?family=Francois+One);

@font-face {
	font-family: 'Nexa Bold';
	font-weight: bold;
	font-style: normal;
	src: url('../fonts/nexa_bold/Nexa_Free_Bold-webfont.eot');
	src: url('../fonts/nexa_bold/Nexa_Free_Bold-webfont.eot?#iefix') format('embedded-opentype'), url('../fonts/nexa_bold/Nexa_Free_Bold-webfont.woff2') format('woff2'), url('../fonts/nexa_bold/Nexa_Free_Bold-webfont.woff') format('woff'), url('../fonts/nexa_bold/Nexa_Free_Bold-webfont.ttf') format('truetype'), url('../fonts/nexa_bold/Nexa_Free_Bold-webfont.svg#nexa_boldregular') format('svg');
}

